import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import personalInfo from "../data/personalInfo";
import helpIntersection2 from "../helpers/helpIntersection2";

const skills = {
  es: {
    name: "Habilidades",
    text: `Estas son algunas de la herramientas, lenguajes, habilidades que manejo, esta lista siempre esta en constante crecimiento, por lo tanto quizs an no s lo que estas buscando, pero puedo aprenderlo .`,
    hard: "Hard Skills",
    soft: "Soft Skills",
  },
  en: {
    name: "Skills",
    text: `Estas son algunas de la herramientas, lenguajes, habilidades que uso para desarrollar, esta lista siempre esta en constante crecimiento, por lo tanto quizs an no s lo que estas buscando, pero puedo aprenderlo .`,
    hard: "Hard Skills",
    soft: "Soft Skills",
  },
};

const Skills = () => {
  const lang = useSelector((state) => state.language.translation);
  const { name, text, hard, soft } = skills[lang];
  const { hardSkills, softSkills } = personalInfo.skills;

  useEffect(() => {
    helpIntersection2(".about-info__hard-skill");
  }, []);

  return (
    <div>
      {" "}
      <h3 className="about-info__title">{name}</h3>
      <p
        className="about-info__text"
        dangerouslySetInnerHTML={{ __html: text }}
      ></p>
      <ul className="about__list about__list--skills">
        <li className="about__item about__item--hard-skills">
          <span className="about__subtitle">{hard}</span>
          <ul className="about-info__hard-skills">
            {hardSkills.map((e, i) => {
              const Icon = e.icon;
              let delay = i + "00";
              return (
                <li
                  className={`about-info__hard-skill ${e.span.toLowerCase()}`}
                  key={i}
                  style={{
                    transitiondelay: delay,
                  }}
                >
                  <Icon modifier="hard-skill__icon" />
                  <span className="hard-skill__name">{e.span}</span>
                </li>
              );
            })}
          </ul>
        </li>
        <li className="about__item">
          <span className="about__subtitle">{soft}</span>
          <ul className="about-info__soft-skills">
            {softSkills[lang].map((e, i) => (
              <li className="about-info__soft-skill" key={i}>
                <span className="soft-skill__title">{e.title}</span>
                <p
                  className="soft-skill__text"
                  dangerouslySetInnerHTML={{ __html: e.text }}
                ></p>
              </li>
            ))}
          </ul>
        </li>
      </ul>
    </div>
  );
};

export default Skills;
